.logo {
  &__wrapper {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;

    span {
      font-size: 1.2rem;
      font-weight: 400;
    }

    @media only screen and (max-width: 450px) {
      align-items: center;
    }
  }
}