.logo {
  &-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &-box {
    display: flex;
    letter-spacing: 2px;
    font-size: 3rem;
    font-weight: 700;
  }

  &-image {
    width: 3.5rem;
  }

  &-imagewrapper {
    margin: 0 2px;
    margin-top: -5px;
  }

  &-sublogo {
    font-size: 1.2rem;
    letter-spacing: 2px;
    margin-top: -1.2rem;
  }
}