@import '../../../../Variables.scss';

.heading {
  margin-top: 12rem;
  margin-bottom: 3rem;
  font-size: 3.6rem;
  font-weight: 900;
  text-align: center;
  text-transform: uppercase;

  span {
    color: $color-primary;
  }

  @media only screen and (max-width: 450px) {
    margin-top: 4rem;
    margin-bottom: 2rem;
  }

  &__sub {
    margin-bottom: 8rem;
    text-align: center;

    @media only screen and (max-width: 450px) {
      margin-bottom: 2rem;
    }
  }
}